.default {
    border-radius: 5px;
    border: none;
    padding: .4em .8em;
    cursor: pointer;
    position: relative;
    display: inline-block;
    font-size: 90%;
    font-weight: 700;
    color: #E5E5E5;
    text-decoration: none;
    text-shadow: 0 -1px 2px rgba(0, 0, 0, .2);
    outline: none;
    background: linear-gradient(rgb(110, 112, 120), rgb(81, 81, 86)) rgb(110, 112, 120);
    box-shadow: 0 1px rgba(255, 255, 255, .2) inset,
    0 3px 5px rgba(0, 1, 6, .5),
    0 0 1px 1px rgba(0, 1, 6, .2);
    transition: .2s ease-in-out;
}

.default:hover:not(:active) {
    background: linear-gradient(rgb(164, 164, 176), rgb(70, 71, 76)) rgb(159, 159, 171);
}

.default:active {
    top: 1px;
    background: linear-gradient(rgb(76, 77, 82), rgb(56, 57, 62)) rgb(76, 77, 82);
    box-shadow: 0 0 1px rgba(0, 0, 0, .5) inset,
    0 2px 3px rgba(0, 0, 0, .5) inset,
    0 1px 1px rgba(255, 255, 255, .1);
}

.default:disabled, .default:disabled:hover, .red:disabled, .red:disabled:hover {
    cursor: default;
    background: linear-gradient(#dedede, #797979);
}

.red {
    border-radius: 5px;
    border: none;
    padding: .4em .8em;
    cursor: pointer;
    position: relative;
    display: inline-block;
    font-size: 90%;
    font-weight: 700;
    color: #E5E5E5;
    text-decoration: none;
    text-shadow: 0 -1px 2px rgba(0, 0, 0, .2);
    outline: none;
    background: linear-gradient(rgb(121, 38, 38), rgb(88, 26, 26)) rgb(121, 38, 38);
    cursor: pointer;
    box-shadow: 0 1px rgba(255, 255, 255, .2) inset,
    0 3px 5px rgba(0, 1, 6, .5),
    0 0 1px 1px rgba(0, 1, 6, .2);
    transition: .2s ease-in-out;
}

.red:hover:not(:active) {
    background: linear-gradient(#cd0f0f, #4e0606);
}

.red:active {
    top: 1px;
    background: linear-gradient(rgb(82, 19, 19), rgb(76, 10, 10)) rgb(82, 19, 19);
    box-shadow: 0 0 1px rgba(0, 0, 0, .5) inset,
    0 2px 3px rgba(0, 0, 0, .5) inset,
    0 1px 1px rgba(255, 255, 255, .1);
}